import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import Education from '../Education'
import useStyles from '../useStyles'

function EducationListItem (props) {
  const {
    school,
    place,
    from,
    to,
    info
  } = props

  const classes = useStyles()

  return (
    <Grid item>
      <Grid
        className={classes.text}
        container
        direction='column'
        justify='center'
        alignItems='stretch'
      >
        <Typography variant='h3'>{school}</Typography>
        <Typography variant='h4'>{place}</Typography>
        <Typography variant='h4'>{from} - {to}</Typography>
        <Typography variant='h4'>{info.map(info => <Typography>{info}</Typography>)}</Typography>

      </Grid>

    </Grid>
  )
}

export default EducationListItem
